pre[class*="language-"] {
  color: lab(64% 103 0);
  text-shadow: 0 0 10px lab(64% 103 0 / .5);
  text-align: left;
  white-space: pre-wrap;
  word-spacing: normal;
  word-break: normal;
  word-wrap: break-word;
  tab-size: 4;
  hyphens: none;
  background: #ffffff0d;
  border-radius: 8px;
  margin: .5em 0;
  padding: 1em;
  font-family: Consolas, Monaco, Andale Mono, Ubuntu Mono, monospace;
  font-size: 13px;
  line-height: 1.5;
  display: block;
  overflow: auto;
}

:not(pre) > code[class*="language-"] {
  white-space: normal;
  border-radius: .3em;
  padding: .1em;
}

.token.comment, .token.block-comment, .token.prolog, .token.doctype, .token.cdata {
  color: #8e8e8e;
  text-shadow: none;
}

.token.punctuation {
  color: #ccc;
}

.token.tag, .token.attr-name, .token.namespace, .token.number, .token.unit, .token.hexcode, .token.deleted, .token.function {
  color: lch(65% 85 35);
  text-shadow: 0 0 10px lch(65% 85 35 / .5);
}

.token.property, .token.selector {
  color: lch(85% 58 205);
  text-shadow: 0 0 10px lch(85% 58 205 / .5);
}

.token.function-name {
  color: #6196cc;
}

.token.boolean, .token.selector .token.id {
  color: #fdfdfd;
  text-shadow: 0 0 2px #001716, 0 0 3px #03edf975, 0 0 5px #03edf975, 0 0 8px #03edf975;
}

.token.class-name {
  color: #fff5f6;
  text-shadow: 0 0 2px #000, 0 0 10px #fc1f2c75, 0 0 5px #fc1f2c75, 0 0 25px #fc1f2c75;
}

.token.constant, .token.symbol {
  color: lab(64% 103 0);
  text-shadow: 0 0 2px #100c0f, 0 0 5px #dc078e33, 0 0 10px #fff3;
}

.token.important, .token.atrule, .token.keyword, .token.selector .token.class, .token.builtin {
  color: #f4eee4;
  text-shadow: 0 0 2px #393a33, 0 0 8px #f39f0575, 0 0 2px #f39f0575;
}

.token.string, .token.string-property, .token.char, .token.attr-value, .token.regex, .token.variable, .token.url {
  color: lch(85% 82.34 80.104);
  text-shadow: 0 0 10px lch(85% 82.34 80.104 / .5);
}

.token.operator, .token.entity {
  color: #67cdcc;
}

.token.important, .token.bold {
  font-weight: bold;
}

.token.italic {
  font-style: italic;
}

.token.entity {
  cursor: help;
}

.token.inserted {
  color: green;
}

html {
  color-scheme: dark;
  --gold: lch(80% 82.34 80.104);
  --gold-text: lch(85% 82.34 80.104);
  --gold-shadow: lch(80% 82.34 80.104 / .7);
  background: #111;
  font-family: system-ui;
}

@font-face {
  font-family: din-1451-lt-pro-engschrift;
  src: url("https://use.typekit.net/af/7fa6e1/00000000000000007735bbcd/30/l?primer=388f68b35a7cbf1ee3543172445c23e26935269fadd3b392a13ac7b2903677eb&fvd=n4&v=3") format("woff2"), url("https://use.typekit.net/af/7fa6e1/00000000000000007735bbcd/30/d?primer=388f68b35a7cbf1ee3543172445c23e26935269fadd3b392a13ac7b2903677eb&fvd=n4&v=3") format("woff"), url("https://use.typekit.net/af/7fa6e1/00000000000000007735bbcd/30/a?primer=388f68b35a7cbf1ee3543172445c23e26935269fadd3b392a13ac7b2903677eb&fvd=n4&v=3") format("opentype");
