header {
  background: radial-gradient(closest-side, lch(80% 82.34 80.104 / .25), #0000);
  grid-area: header;
  grid-template-areas: "logo header"
                       "logo subheader"
                       ". links";
  column-gap: 30px;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  padding: 50px 0;
  font-size: 16px;
  display: grid;
}

header svg {
  filter: drop-shadow(0 0 5px var(--gold-shadow)) drop-shadow(0 0 15px var(--gold-shadow));
  grid-area: logo;
  place-self: center end;
  width: 50px;
}

header svg .outer {
  stroke-width: 30px;
  stroke: var(--gold);
}

header svg .inner {
  fill: lch(100% 82.34 80.104);
}

header .title {
  -webkit-text-stroke: 2px var(--gold-text);
  color: #0000;
  filter: drop-shadow(0 0 3px var(--gold-shadow)) drop-shadow(0 0 10px var(--gold));
  letter-spacing: -.02em;
  margin: 0;
  font-family: urbane-rounded, ui-rounded;
  font-size: 60px;
  font-weight: 600;
  text-decoration: none;
}

header .title::selection {
  -webkit-text-stroke-color: #fffddd;
  background-color: var(--gold-text);
}

h1, h2, h3 {
  color: lch(65% 85 35);
  letter-spacing: -.02em;
  margin: 2em 0 .5em;
  font-family: urbane-rounded, ui-rounded;
  font-weight: 600;
}

h1 {
  margin-top: 0;
}

header p {
  grid-area: links;
  margin: 0;
}

header p a {
  color: lch(90% 50.34 80.104);
  filter: drop-shadow(0 0 8px lch(90% 50.34 80.104 / .7));
  text-underline-offset: 2px;
  -webkit-text-decoration-skip-ink: none;
  text-decoration-skip-ink: none;
  font-family: urbane-rounded, ui-rounded;
  font-size: 1em;
  font-weight: 600;
  text-decoration-style: wavy;
  text-decoration-color: lch(90% 50.34 80.104 / 0);
  text-decoration-thickness: 2px;
  transition: text-decoration-color .15s;
}

header a:hover {
  text-decoration-color: lch(90% 50.34 80.104);
}

@media (width < 500px) {
  header {
    text-align: center;
    grid-template-areas: "logo"
                         "header"
                         "subheader"
                         "links";
    place-items: center;
    gap: 8px;
  }

  header .title {
    -webkit-text-stroke-width: 1.5px;
    padding: 0;
    font-size: 38px;
  }

  header h2 {
    font-size: 14px;
  }

  header p a {
    font-size: 13px;
  }

  header svg {
    place-self: center;
  }
}

body {
  --body-padding: 20px;
  padding: 0 var(--body-padding);
  grid-template-columns: 180px 1fr;
  grid-template-areas: "header header"
                       "nav main"
                       "footer footer";
  gap: 40px;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
  display: grid;
}

main {
  grid-area: main;
  max-width: 800px;
  padding-right: 240px;
  position: relative;
}

p, li {
  line-height: 1.5em;
}

p:empty {
  display: none;
}

a {
  color: lch(85% 58 205);
}

nav {
  text-align: end;
  border-right: 1px solid lch(90% 50.34 80.104 / .1);
  grid-area: nav;
  height: -moz-fit-content;
  height: fit-content;
  padding-right: 20px;
  position: sticky;
  top: 40px;
}

nav h3, .table-of-contents h3 {
  margin-top: 0;
}

main > aside {
  position: sticky;
  top: 40px;
}

.table-of-contents {
  border-left: 1px solid lch(90% 50.34 80.104 / .1);
  max-height: calc(100vh - 80px);
  margin-left: 40px;
  padding-left: 20px;
  position: absolute;
  left: 100%;
  overflow: auto;
}

.table-of-contents ul, nav ul {
  padding-left: 2ch;
  list-style: none;
}

.table-of-contents > ul {
  width: 180px;
  margin: 0;
  padding: 0;
}

nav > ul {
  margin: 0;
  padding: 0;
}

.table-of-contents li, nav li {
  margin: 6px 0;
  line-height: 1em;
}

.table-of-contents a, nav a {
  color: lch(90% 50.34 80.104);
  font-family: urbane-rounded;
  font-size: 14px;
  text-decoration: none;
}

.table-of-contents a:hover, .table-of-contents a[aria-current], nav a:hover, nav a[aria-current] {
  color: var(--gold-text);
}

a[aria-current] {
  text-decoration: underline;
}

.features {
  column-count: 2;
}

@media (width < 1040px) {
  .table-of-contents {
    display: none;
  }

  main {
    padding-right: 0;
  }

  .features {
    column-count: 1;
  }
}

@media (width < 600px) {
  body {
    width: auto;
    display: block;
  }

  nav {
    text-align: start;
    border-bottom: 1px solid lch(90% 50.34 80.104 / .1);
    border-right: none;
    padding-bottom: 20px;
    position: static;
  }
}

.warning {
  background: lch(80% 82.34 80.104 / .15);
  border: 4px solid lch(70% 82.34 80.104);
  border-radius: 8px;
  margin: 20px 0;
  padding: 20px;
}

.warning > :first-child {
  margin-top: 0;
}

.warning > :last-child {
  margin-bottom: 0;
}

.warning pre {
  background: #000000a6;
}

.warning :is(h1, h2, h3) {
  color: #fff;
}

footer {
  color: #666;
  text-align: center;
  grid-area: footer;
  padding-bottom: 20px;
  font-size: 12px;
}

